import React, {useState} from 'react';
import {connect} from 'react-redux';
import styled from 'styled-components';
import downIcon from "../../static/img/down_icon.svg"
import {Menu, Dropdown} from 'antd';
//actions
import {switchLang} from "../../Redux/actions/lang";
import {getBiographyHome} from "../../Redux/actions/BiographyHome";
import {getInfos} from "../../Redux/actions/HeaderInfo";
import {getConcertsHome} from "../../Redux/actions/ConcertsHome";
import {getTimeline} from "../../Redux/actions/Timeline";
import {getPress} from "../../Redux/actions/getPress";
import {getConcertsDetailsPage} from "../../Redux/actions/ConcertsDetails";

function LangPack(props) {

    const [rotate, setRotate] = useState(0);

    const imgStyle = {
        marginLeft: 8,
        marginTop: rotate ? -4 : -2,
        transition: 'all .2s linear',
        transform: `rotate(${rotate ? 180 : 0}deg)`
    }

    function onLangSwitch(lang) {
        props.switchLang(lang);
        props.getBiographyHome(lang);
        props.getInfos(lang);
        props.getConcertsHome(lang);
        props.getTimeline(lang);
        props.getPress(lang);
        props.getConcertsDetailsPage(lang);

        setRotate(0);
    }

    function rotation() {
        setRotate(!rotate);
    }


    const menu = (
        <Menu style={{marginTop: 3}}>
            {
                props.lang == 'en' ? null : <Menu.Item key="0" onClick={() => onLangSwitch("en")}>
                    En
                </Menu.Item>
            }
            {
                props.lang == 'ru' ? null :
                    <Menu.Item key="1" onClick={() => onLangSwitch("ru")}>
                        Ru
                    </Menu.Item>
            }
            {
                props.lang == 'az' ? null :
                    <Menu.Item key="2" onClick={() => onLangSwitch("az")}>
                        Az
                    </Menu.Item>
            }
            {
                props.lang == 'de' ? null :
                    <Menu.Item key="3" onClick={() => onLangSwitch("de")} style={{marginLeft: 0}}>
                        De
                    </Menu.Item>
            }
        </Menu>
    );

    return (
        <Container>
            <Dropdown overlay={menu} trigger={['click']} getPopupContainer={trigger => trigger.parentNode}>
                <a className="ant-dropdown-link"
                   style={{color: '#fff', textDecoration: 'none', fontFamily: 'Montserrat-Bold'}}
                   href="#" onClick={() => rotation()}>
                    {props.lang && props.lang.toUpperCase()}
                    <img src={downIcon} alt=""
                         style={imgStyle}/>
                    {/*<i className="fas fa-chevron-down"/>*/}
                    {/*<Icon type="down" style={{fontSize:20}}/>*/}
                </a>
            </Dropdown>
        </Container>
    );
}


const Container = styled.div`
  width: 100%; 
  margin-right: 60px;
`;

const DropDown = styled.div`
 color: #fff !important;
 font-size: 20px !important;
 text-transform: uppercase;  
  font-family: Montserrat-Bold !important;
`;

function mapStateToProps(state) {
    return {
        lang: state.lang.lang
    }
}

export default connect(mapStateToProps, {switchLang, getBiographyHome, getInfos,getConcertsHome,getTimeline,getPress,getConcertsDetailsPage})(LangPack);
