import React, {Fragment, useEffect} from 'react';
import {connect} from 'react-redux'
import styled from 'styled-components';
import {getInfos} from '../../../Redux/actions/HeaderInfo'
function Info(props) {

    useEffect(() => {
        props.getInfos(props.lang);
    }, []);

    return (
       <Fragment>
           {
               props.infos.slice(0, 3).map((item,index)=>{
                   return (
                       <Container className="col-6 col-md-12 mx-lg-auto" key={index}>
                           <div className="box-shadow">
                               <p>{item.city}</p>
                               <p>{item.hall}</p>
                               <span>{item.date}</span>
                           </div>
                       </Container>
                   )
               })
           }
       </Fragment>
    )
}


const Container = styled.div`
  flex: 1; 
  margin: 0 0 30px;
  &:last-child{
  margin-bottom: 0;
  }
  color: #fff;
  font-family: AngleciaProDisplay-Regular;
 
  div{
     background-color: #0f0f0f;
     padding: 25px 25px 50px;
  }
  p:first-child{
      font-family: AngleciaProDisplay-Regular;
      color: #fff !important;
      font-size: 32px;
      margin-bottom: 10px;
  }
  p:last-child{
      font-family: AngleciaProDisplay-Regular;
      font-size: 20px;
      margin-bottom: 0px !important;
  } 
  span{
      font-family: AngleciaProDisplay-Regular;
      font-size: 16px;
  }
  @media(max-width: 768px){
      padding: 15px 15px;
      h4{
      font-size: 16px;
      }
      p{
      font-size:10px
      }
      margin-bottom: 15px;
  }
`;

function mapStateToProps(state) {
    return {
        infos: state.infos,
        lang:state.lang.lang
    };
}
export default connect(mapStateToProps, {getInfos})(Info);
