import React from 'react';
import styled from 'styled-components';


function PressCard(props) {

    return (
        <Container className="col-6 col-sm-4 col-md-4 col-lg-4 col-xl-3">
            <PressWrap image={props.image} className="box-shadow">

                <Overlay>
                    <div className="press-item">
                        <h3>{props.name}</h3>
                        <div className="press-info">
                            <a href="https://yenicag.ru/" target="_blank">{props.web}</a>
                            <p>{props.date}</p>
                        </div>
                    </div>
                </Overlay>
            </PressWrap>
        </Container>
    );
}


const Container = styled.div`
  width: 100%; 
  //margin-bottom: 50px;
  .col-6,.col-md-4,.col-lg-3{
  position: static !important;
  z-index: -2;
  }
  .col-12, .col-md-4, .col-xl-3{
  position: static !important;
  z-index: -1;
  }
    @media screen and (max-width: 1200px){ 
         &:last-child{
            display: none;
         }
     }
    @media screen and (max-width: 576px){
     &:nth-last-child(-n+2) {
     display: none;
     } 
  } 
   
`;

const PressWrap = styled.div` 
    background-image: url(${props => require(`../../../static/img/press/${props.image}`)});
    height: 395px;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
     @media(max-width: 1200px){
        height: 231px !important;
        max-width: 231px;
        }
    @media(max-width: 568px){
        height: 164px;
        max-width: 164px;
        //padding: 10px;
    } 
`;

const Overlay = styled.div`
font-family: AngleciaProDisplay-Regular;
width: 100%;
height: 100%;
padding: 24px 5px 24px 24px;
@media screen and (max-width: 1200px){ 
  padding: 17px 17px 9px 5px;
}
@media screen and (max-width: 576px){
  &:nth-last-child(-n+2) {
    padding: 10px 12px 5px 5px;
  } 
} 
:hover{
    transition: .3s;
    background-color: rgba(0,0,0,.5); 
} 

 .press-item{ 
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: center; 
    }
    h3{
        font-size: 32px;
        color: #fff;
        text-align: left !important; 
        margin-bottom: 0 !important;
           @media(max-width: 1200px){
           font-size: 22px !important;
           }
            @media(max-width: 769px){
            font-size: 16px !important;
            margin-bottom: 25px !important;
        }
    }

    .press-info{
        a{
            font-size: 20px;
            color: #fff !important;
            text-decoration: none !important;
            text-align: center;
            @media(max-width: 1200px){
           font-size: 16px;
           }
            @media(max-width: 769px){
                font-size: 12px !important;
            }
           
        }
        
         p{
              @media(max-width: 1200px){
                font-size: 16px;
              }
              @media(max-width: 576px){
                font-size: 8px !important;
              }
            } 
    }
`;

export default PressCard;
