export const getConcertsDetailsPage = (lang) => async (dispatch) => {
    const concerts = await fetch(`http://fuadib.mircloud.host/api/concerts/info/${lang}`).then(res => res.json());

    dispatch(getConcertsDispatch(concerts))
};

const getConcertsDispatch = (concerts) => ({
    type: "GET_CONCERTS",
    concerts
});
