import React, {useEffect} from 'react';
import styled from 'styled-components';
import DetailsButton from "../../Common/DetailsButton";
import {connect} from "react-redux";
import {getBiographyHome} from "../../../Redux/actions/BiographyHome"
import {FormattedMessage} from "react-intl";


function Biography(props) {

    useEffect(() => {
        props.getBiographyHome(props.lang);
    }, []);

    const truncate = (str) => {
        return str.length > 200 ? str.substring(0, 280) + '...' : str;
    };

    const {description} = props.biographyHome;
    return (
        <>
            <Container id="biography" className="container-fluid">
                <TextWrap>
                    <h3><FormattedMessage id="biography"/></h3>
                    <p>
                        {description ? truncate(description) : null}
                    </p>

                    <DetailsButton text="Read more" route="biography"/>
                </TextWrap>
            </Container>
        </>
    );
}


const Container = styled.div` 
   min-height: calc(100vh - 183px);
   width: 100%;  
   display: flex;
   align-items: center;
  padding-top: 100px;
  padding-bottom: 100px;
 
`;

const TextWrap = styled.div`
  width: 100%;
  max-width: 1080px;
  text-align: center;
  margin: 0 auto;
  padding: 0 20px;
  color: #fff;
  @media(max-width: 768px){
     //padding-bottom: 100px;
  }
 h3 {
      font-size: 32px;
      text-align: center;
      color: #fff;
      margin-bottom: 50px;
      text-transform: capitalize;
      @media screen and (max-width: 1200px){ 
        margin-bottom: 32px; 
      }
      @media screen and (max-width: 576px){
        margin-bottom: 24px;
        font-size: 18px !important; 
      } 
      @media screen and (max-width: 992px){
      font-size: 24px
      } 
   }
  p {
    font-size: 24px;
     @media(max-width: 768px){
        font-size: 16px;
     }
    margin: 0;
  }
`;

function mapStateToProps(state) {
    return {
        biographyHome: state.biographyHome,
        lang: state.lang.lang
    };
}

export default connect(mapStateToProps, {getBiographyHome})(Biography);
