import concertsHome from './ConcertsHome'
import timeline from './Timeline'
import press from './Press';
import {index} from './setIndexPage'
import concerts from './ConcertsDetails'

export default combineReducers({
    lang,
    scrollTo,
    posts,
    infos,
    biographyHome,
    concertsHome,
    timeline,
    press,
    index,
    concerts
});
