import {connect} from "react-redux";
import {getBiographyHome} from "../../Redux/actions/BiographyHome"
import {FormattedMessage} from "react-intl";



function Biography({biographyHome, getBiographyHome, lang}) {

    let myRef = useRef();

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    useEffect(() => {
        getBiographyHome(lang);
    }, []);



    const {media, description} = biographyHome;


    return (
        <>
            <Container className="pt-72">
                <div className="col-md-6 mx-auto">
                    <h3 ref={myRef}>
                        <FormattedMessage id="biography"/>
                    </h3>
                    <img src={biographyImg} alt="Fuad Ibrahimov biography"/>
                    {/*<img src={`../../static/img/biography/${media}`} alt="Fuad Ibrahimov biography"/>*/}
                    <p>{description}</p>
                </div>
            </Container>
            <Footer main={false} />
        </>
    );
}


