import React, {useState} from 'react';
import {Route} from "react-router-dom";
import styled from "styled-components";
import {IntlProvider} from 'react-intl';
import {connect} from 'react-redux';
import Nav from './Components/Common/Nav'
import Footer from './Components/Common/Footer'
import Main from "./Components/Main";
import Biography from "./Components/Biography/Biography";
import Concerts from "./Components/Concerts/Concerts";
import Sound from "./Components/Common/Sound";
import Media from "./Components/Media/Media";
import Press from "./Components/Press/Press";
// translation
import translation from './translation';


function App({location, lang}) {
    const [sound, setSound] = useState({});
    const [nav, setNav] = useState({});
    const [size, setSize] = useState(window.matchMedia("(max-width: 1200px)").matches);


    function getRefElement(el, cat) {
        if (cat === "sound") setSound(el);
        if (cat === "nav") setNav(el);
    }

    function getLast(value) {
        if (value) {
            sound.current.style.bottom = size ? '55px' : '90px';
            nav.current.style.transform = size?"translateY(-55px)":"translateY(-90px)";
        } else {
            sound.current.style.bottom = '0';
            nav.current.style.transform = "translateY(0)";
        }
    }


    return (
        <IntlProvider locale={lang.lang} messages={translation[lang.lang]}>
            <Container>
                <Nav location={location} getRefElement={getRefElement} />
                <Sound getRefElement={getRefElement} location={location} />
                <Route exact path="/" render={() => <Main getLast={getLast} />}/>
                <Route exact path="/biography" component={Biography}/>
                <Route exact path="/concerts" component={Concerts}/>
                <Route exact path="/media" component={Media}/>
                <Route exact path="/press" component={Press}/>
                {/*<Route exact path="/timeline" component={Timeline}/>*/}
                {/*<Route exact path="/contact" component={Contact}/>*/}
            </Container>
            {location.pathname === "/" && <Footer location={location} getRefElement={getRefElement} main={true} />}
        </IntlProvider>
    );
}

function mapStateToProps(state) {
    return {
        lang: state.lang
    }
}

export default connect(mapStateToProps)(App);


const Container = styled.div`
  min-height: calc(100vh - 90px);
  overflow-x: hidden;
`;

