export const switchLang = (lang) => (dispatch) => {
    localStorage.setItem("lang", lang);
  dispatch(switchLangDispatch(lang))
};

export const switchLangDispatch = (lang) => ({
    type: "SWITCH_LANG",
    lang
});

export const scrollTo = (num) => dispatch => {
    dispatch(scrollToDispatch(num))
};

const scrollToDispatch = (num) => ({
    type: "SCROLL_TO",
    num
});
