import React, {useEffect} from 'react';
import {Tab, TabPanel, Tabs, TabList} from "react-web-tabs";
import 'react-web-tabs/dist/react-web-tabs.min.css'
import {connect} from "react-redux";
import {getTimeline} from "../../../Redux/actions/Timeline"
import styled from 'styled-components';


function Timeline(props) {

    useEffect(() => {
        props.getTimeline(props.lang);
    }, []);

    return (
        <Container className="col-md-10 mx-auto" id="timeline">
            <h3>Timeline</h3>
            <Tabs defaultTab="vertical-tab-0" vertical className="vertical-tabs">
                <div className="row w-100">
                    <div className="col-md-12 col-lg-6">
                        <TabList>
                            <div className="a-c">
                                {
                                    props.timeline.map((item, index) => {
                                        return (
                                            <Tab key={index} tabFor={`vertical-tab-${index}`}>{item.title}</Tab>
                                        )
                                    })
                                }
                            </div>
                        </TabList>
                    </div>
                    <div className="col-lg-6">
                        {
                            props.timeline.map((item, index) => {
                                    return (
                                        <TabPanel key={index} tabId={`vertical-tab-${index}`}>
                                            <p>   {
                                                item.description
                                            }</p>
                                        </TabPanel>
                                    )
                                }
                            )
                        }
                    </div>
                </div>
            </Tabs>
        </Container>
    );
}


