import React, {useEffect, useState, useCallback, useRef} from 'react';
import {withRouter} from 'react-router';
import {connect} from 'react-redux';
//components
import Header from "./Header/Header";
import Biography from "./Biography/Biography";
import Concerts from "./Concerts/Concerts";
import Press from "./Press/Press";
import Timeline from "./Timeline/Timeline";
import Media from "./Media/Media";
import Contact from "./Contact/Contact";
import "fullpage.js/vendors/easings"
import "fullpage.js/vendors/scrolloverflow";
import ReactFullpage from "@fullpage/react-fullpage";
import {indexPage} from "../../Redux/actions/setIndexPage";
import $ from 'jquery';


const FullpageWrapper = (props) => {
    const [moveTo, setMoveTo] = useState(false);
    const [moveToContact, setMoveToContact] = useState(false);
    const [sectionNum, setSectionNum] = useState(null);
    const [test, setTest] = useState(0);
    let myRef = useRef();
    const fp = useRef();

    useEffect(() => {
        setSectionNum(props.scrollNum);

    }, [props.scrollNum]);

    useEffect(() => {
        if (props.location.isScroll) {
            setMoveTo(props.location.isScroll)
        }
        if (props.location.isScrollContact) {
            setMoveToContact(props.location.isScrollContact)
        }



    }, []);


    const onChange = useCallback((num) => {
        setTest(num);
    }, []);

    // const scrollToRef = (ref) => window.scrollTo(0, ref.current.offsetTop)
    const _onAfterLoad = (e, dest, direction) => {
        props.getLast(dest.isLast);

        setSectionNum(null);
        props.indexPage(dest.index);
        let index = props.index;
        onChange(dest.index);
        if (dest.index === 1) {
            // $('.biography-section').scrollTop(0);
            // $("#fullpage-wrapper").css({ background: 'red' })
        }
    };

    function _onLeave(e, dest, direction) {
        if (!dest.isLast) {
            props.getLast(dest.isLast);
        }

        setMoveTo(false);
        setMoveToContact(false);

    };

    return (
        <ReactFullpage
            licenseKey={'A5628A30-7C8748DF-A79A5C72-9BAC7D5A'}
            scrollOverflowResetKey={'A5628A30-7C8748DF-A79A5C72-9BAC7D5A'}
            id="fp"
            scrollingSpeed={800}
            verticalCentered={true}
            easingcss3={'cubic-bezier(0.25, 0.46, 0.45, 0.94)'}
            afterLoad={_onAfterLoad}
            onLeave={_onLeave}
            scrollOverflow={true}
            click={true}
            // scrollOverflowOptions={click=true}
            scrollOverflowReset={true}
            render={({state, fullpageApi}) => {
            if (sectionNum !== null) {
                fullpageApi !== undefined && fullpageApi.moveTo(sectionNum);
            }
            fullpageApi !== undefined && moveTo && fullpageApi.silentMoveTo(5);
            fullpageApi !== undefined && moveToContact && fullpageApi.silentMoveTo(7);
            return (
                <ReactFullpage.Wrapper>
                    <div className="section">
                        <Header/>
                    </div>
                    <div className="section biography-section" ref={myRef}>
                        <Biography/>
                    </div>
                    <div className="section">
                        <Concerts/>
                    </div>
                    <div className="section">
                        <Press/>
                    </div>
                    <div className="section">
                        <Timeline/>
                    </div>
                    <div className="section">
                        <Media/>
                    </div>
                    <div className="section">
                        <Contact/>
                    </div>
                </ReactFullpage.Wrapper>
            );
        }}
        />
    )
};

function mapStateToProps(state) {
    return {
        scrollNum: state.scrollTo,
        index: state.index
    }
}

export default connect(mapStateToProps, {indexPage})(withRouter(FullpageWrapper));
