import React from 'react';
import styled from 'styled-components';

function ConcertCard(props) {
    return (
        <Container className="col-6 col-sm-4 col-md-4 col-lg-4 col-xl-3">

            <CardWrap image={props.image}>

                <div>
                    <h3>{props.city}</h3>
                    <p>{props.hall}</p>
                    <span>{props.date}</span>
                </div>
            </CardWrap>

        </Container>
    );
}

const Container = styled.div` 
 @media(max-width: 1200px){ 
     :last-child{
         display: none;
     }
}
 @media(max-width: 576px){
     &:nth-last-child(-n+2) {
         display: none;
     }
 }
 :first-child::after{
      color:#fff;
      content: 'Next Concert';
      position:absolute;
      bottom: 20px;
      left: 30px; 
      font-size: 24px;
      @media(max-width: 1200px){
        font-size: 12px !important;
      }
 }
`;

const CardWrap = styled.div`
 width: 100%;
 height: 500px;
// @media screen and ( max-height: 899px ) {
//    height: 440px;
//
//}
 @media(max-width: 1200px){ 
     &:last-child{
         height: 410px !important;
     }
}
 @media(max-width: 576px){
     &:nth-last-child(-n+2) {
         height: 290px !important;
     }
 }
  background-image: url(${props => require(`../../../static/img/${props.image}`)}) !important;
  background-size: cover;
  background-position: center;
  box-shadow: 0 2px 4px black;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #fff;
  position: relative;
  background-color: red !important; 
  @media(max-width: 992px){ 
  height: 410px !important;
  }
  @media(max-width: 600px){
  margin-top: 0;
  height: 290px !important;
  }
  div:first-child {
    display: flex;
    flex-direction: column;
    padding: 20px;
   
  h3 {
      font-family: AngleciaProDisplay-Regular !important;
      font-size: 32px;
      margin: 0;
      text-align: left;
      text-shadow: 0 2px 4px 0 #000; 
        @media(max-width: 1200px){
         font-size: 24px !important;
        }
        @media(max-width: 576px){
          font-size: 16px !important;
        }
  }
  p {
      font-size: 20px;
      font-family: AngleciaProDisplay-Regular !important;
      margin-bottom: 0;
       @media(max-width: 1200px){
         font-size: 16px !important;
       }
        @media(max-width: 576px){
         font-size: 16px !important;
       }
  }
    span {
      font-family: AngleciaProDisplay-Regular !important;
      font-size: 16px;
       @media(max-width: 992px){
         font-size: 10px !important;
       }
    }
  } 
`;

