export const getBiographyHome = (lang) => async (dispatch) => {
    const biographyHome = await fetch(`http://fuadib.mircloud.host/api/biography/${lang}`).then(res => res.json());
    dispatch(getBiographyHomeDispatch(biographyHome))
};

const getBiographyHomeDispatch = (biographyHome) => ({
    type: "GET_BIOGRAPHY_HOME",
    biographyHome
});
