function ConcertTable(props) {
    useEffect(() => {
        props.getConcertsDetailsPage(props.lang);
    }, []);

    const [visible, setVisible] = useState({});

    const handleClick = (id) => {
        console.log("handleclivk2", id);
        setVisible({[id]: !visible[id]});
    }

    return (
        <Container className="container-fluid">
            <div className="">
                <div className="col-xl-12">
                    {
                        // props.concerts.map((tb, i) => {
                        props.concerts.map((tb, i) => {
                            return (
                                <Fragment key={i}>
                                    <Main>
                                        <div className="row w-100 align-items-center">
                                            <div className="col-12 col-xl-3">{tb.date}</div>
                                            <div className="col-12 col-xl-4">{tb.hall}</div>
                                            {/*<div className="col-xl-5 d-flex flex-wrap justify-content-between align-items-center w-100">*/}
                                            <div className="col-6 col-xl-2">{tb.city}</div>
                                            <div className="col-6 col-xl-3 text-right p-0">
                                                    {!visible[tb.id] && <button className="info"
                                                                                onClick={() => handleClick(tb.id)}>Info</button>}
                                                    {visible[tb.id] &&
                                                    <Circle onClick={() => handleClick(tb.id)} className="ml-auto"/>}
                                            </div>
                                            {/*</div>*/}

                                        </div>
                                    </Main>
                                    {
                                        visible[tb.id] &&
                                        <Description className="row m-0">
                                            <div className="col-12 col-xl-12 pl-0">
                                                <p>
                                                    {tb.info}
                                                </p>
                                            </div>
                                        </Description>
                                    }
                                </Fragment>
                            )
                        })
                    }
                </div>
            </div>
        </Container>
    );
}


