import React, {Fragment, useEffect, useRef, useState} from 'react';
import styled from 'styled-components';
import music from "../../static/audio/IMSLP77147-PMLP80636-Daphnis_and_Chloe_-_Ravel.mp3";


function Sound(props) {
    const [play, setPlay] = useState(true);
    const [isShown, setIsShown] = useState(false);
    const [screenSize, setScreenSize] = useState(window.matchMedia("(max-width: 1200px)").matches);

    const soundRef = useRef();

    useEffect(() => {
        const isSafari = navigator.vendor && navigator.vendor.indexOf('Apple') > -1;
        const audioEl = document.getElementsByClassName("audio-element")[0];

        if (!isSafari) {
            audioEl.play();
        } else {
            setPlay(false)
        }

        if(screenSize){
            setIsShown(true);
            setPlay(false);
            audioEl.pause();
        }


    }, []);

    // useEffect(() => {
    //     if (props.location.pathname !== "/") {
    //         soundRef.current.style.display = "none";
    //     } else {
    //         soundRef.current.style.display = "flex";
    //     }
    // }, [props.location]);

    useEffect(() => {

        if (Object.keys(props).length !== 0) {
            props.getRefElement(soundRef, 'sound')
        }
    }, []);

    function stopMusic(state){
        console.log('state', state)
        const isSafari = navigator.vendor && navigator.vendor.indexOf('Apple') > -1;
        const audioEl = document.getElementsByClassName("audio-element")[0]
        setPlay(state);

        if (isSafari) {
            if (state) {
                audioEl.play();
            } else {
                audioEl.pause();
            }
        } else {
            if (state) {
                audioEl.play();
            } else {
                audioEl.pause();
            }
        }
    }


    return (
        <Fragment>
            <BottomLine id="float-element" ref={soundRef}>
                <SoundOn>SOUND <CircleIcon/>
                    <span onClick={() => stopMusic(!play)}>{play ? 'ON' : 'OFF'}</span>
                </SoundOn>
                {
                    !screenSize && !isShown &&
                    <SocialText onMouseEnter={() => setIsShown(true)}
                                onMouseLeave={() => setIsShown(false)}>
                        Social
                    </SocialText>
                }
                {
                    isShown && (
                        <Social onMouseEnter={() => setIsShown(true)}
                                onMouseLeave={() => {
                                    if (!screenSize) setIsShown(false)
                                }}>

                            <li>
                                <a href="https://www.facebook.com/fuadiIbrahimov" target="_blank">
                                    <i className="fab fa-twitter"></i>
                                </a>
                            </li>
                            <li>
                                <a href="https://www.facebook.com/fuadiIbrahimov" target="_blank">
                                    <i className="fab fa-youtube"></i>
                                </a>
                            </li>
                            <li>
                                <a href="https://www.facebook.com/fuadiIbrahimov" target="_blank">
                                    <i className="fab fa-instagram"></i>
                                </a>
                            </li>
                            <li>
                                <a href="https://www.facebook.com/fuadiIbrahimov" target="_blank">
                                    <i className="fab fa-facebook-f"></i>
                                </a>
                            </li>
                        </Social>
                    )
                }
            </BottomLine>
            <audio playsInline className="audio-element">
                <source src={music}></source>
            </audio>
        </Fragment>
    );
}


const BottomLine = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  color: #fff;
  position: fixed;
  bottom: 0px;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: 86px;
  transition: all 0.3s ease;
  .title.sc-truncate{
  display: none !important;
  }
  
  @media screen and (max-width: 1200px){
    height: 55px;
  }
  
`;

const CircleIcon = styled.span`
    display: inline-block;
    width: 6px;
    height: 6px;
    border-radius: 100%;
    background-color: #fff;
`;

const SoundOn = styled.span`
    margin-left: 90px;
    display: flex;
    align-items: center; 
    @media screen and (max-width: 1200px){
        font-size: 14px;
        margin-left: 20px;
    }
    
    @media screen and (max-width: 768px){
        font-size: 12px !important;
    }
    span{
        margin: 0 2px 0 4px;
        cursor: pointer;
    }
`;
const Social = styled.ul`
    margin: 0 90px 0; 
    padding: 0;
    list-style: none;
    display: flex;
    align-items: center; 
    transition: 1s ease;
    cursor: pointer; 
    @media screen and (max-width: 992px){
      margin-left: 20px !important; 
    }
    li{ 
        margin-right: 25px; 
        cursor:pointer;
        :last-child{
        margin-right: 0;
    }
    a{
        color: #fff !important;
        font-size: 22px;
       @media screen and (max-width: 992px){
          font-size: 18px;
       }
    }
};


@media screen and (max-width: 1100px){
    font-size: 14px;
    margin-right: 33px;
}

@media screen and (max-width: 768px){
  font-size: 12px !important;
}
`;

const SocialText = styled.div`
    margin: 0 90px 0; 
    
    align-items: center; 
    //padding: 5px 0 5px 5px;
    text-transform: uppercase;
    transition: .9s ease;
     
    @media screen and (max-width: 992px){
        margin-left: 20px !important; 
        display: none;
    }
     :hover{
         cursor:pointer;
         display: none; 
     }
    `;

export default Sound;
