import React, {useRef, useEffect} from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';
import {FormattedMessage} from "react-intl";
import {connect} from 'react-redux'

//actions
import {scrollTo} from "../../Redux/actions/lang";




function Footer(props) {

    const footRef = useRef();

    useEffect(() => {
        if (Object.keys(props).length !== 0 && props.main === true) {
            props.getRefElement(footRef, 'footer')
        }
    }, []);

    function _onScroll(num) {
        props.scrollTo(num);
    }

    function renderFooterMenu({ pathname }) {
        if (pathname === "/") {
           return(
               <Menu>
                   <ul>
                       <li>
                           <span onClick={() => _onScroll(2)}><FormattedMessage id="biography" /></span>
                       </li>
                       <li>
                           <span onClick={() => _onScroll(3)}>CONCERTS</span>
                       </li>
                       <li>
                           <span onClick={() => _onScroll(4)}>Press</span>
                       </li>

                       <li>
                           <span onClick={() => _onScroll(5)}>Timeline</span>
                       </li>
                       <li>
                           <span onClick={() => _onScroll(6)}>MEDIA</span>
                       </li>
                       <li>
                           <span onClick={() => _onScroll(7)}><FormattedMessage id="contact"></FormattedMessage></span>
                       </li>
                   </ul>
               </Menu>
           )
        }
        else{
           return (
               <Menu>
                   <ul>
                       <li>
                           <Link to="/biography"><FormattedMessage id="biography"></FormattedMessage></Link>
                       </li>
                       <li>
                           <Link>TIMELINE</Link>
                       </li>
                       <li>
                           <Link to="/concerts">CONCERTS</Link>
                       </li>
                       <li>
                           <Link to="/press">PRESS</Link>
                       </li>
                       <li>
                           <Link to="/media">MEDIA</Link>
                       </li>
                       <li>
                           <Link to="/contact"><FormattedMessage id="contact"></FormattedMessage></Link>
                       </li>
                   </ul>
               </Menu>
           )
        }
    }

    return (
        <Container id="footer" ref={footRef} className="container-fluid" main={props.main}>
            <div className="col-md-11 mx-auto">
                <div className="row footer">
                    <Logo className="col-xl-3 col-lg col">
                        <div onClick={() => _onScroll(1)}><span>Fuad Ibrahimov</span></div>
                    </Logo>
                    <div className="col-xl-7 hide-menu">
                        {
                            props.location !== undefined && renderFooterMenu(props.location)
                        }
                    </div>
                    <Copyright className="col-xl-2 col-lg col">
                        <span>Copyright </span>
                        <span>2019</span>
                    </Copyright>
                </div>
            </div>
        </Container>
    );
}


const Container = styled.footer`
  width: 100%;
  height: 90px;
  background-color: #000 !important;   
  display:flex;
  align-items: center; 
  position: ${props => props.main ? 'absolute' : 'static'};
  bottom: 0;
  left: 0; 
  @media screen and (max-width: 1200px){
    height: 55px;
  }
  .footer{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #fff;
  padding-top: 15px;
  //position: relative;
  //&::before {
  //  content: '';
  //  position: absolute;
  //  top: -20px;
  //  left: 0;
  //  width: 100%;
  //  height: 1px;
  //  background-color: white;
  //}
   @media screen and (max-width: 1200px){
       padding-top: 5px !important;
      }
  }
  .hide-menu{
  display: flex;
    justify-content: center;
      @media(max-width: 1200px){
        display: none !important;
      }
     
  }
`;

const Logo = styled.div` 

      a:hover{
      text-decoration: none;
      }
      span{
        font-family: "Stephanie-Marie-JF-Regular";
        font-size: 32px;
        color: #fff;
        cursor: pointer;
         @media(max-width: 1200px){
            font-size: 28px;
         }
         @media(max-width: 992px){
            font-size: 16px;
         }
      }
`;

const Menu = styled.div`
font-size: 20px;
font-family: Montserrat-Regular;
     @media (max-width:1200px) {
      display: none;
      }
    ul{
        padding: 0;
        display: flex;
        list-style: none;
        justify-content: space-between;
        margin-bottom: 0;
        li{
            font-size: 20px;
            padding: 10px 20px;
            cursor: pointer;
            //margin: 0 60px 0 0;
            position: relative;
            a,span{
              color: #fff !important;
              text-decoration: none !important;
              font-size: 16px;
              text-transform: uppercase;
            }
             
            &:last-child { 
              padding-right: 0;
            } 
        }
    }
`;

const Copyright = styled.div`
display: flex;
align-items: center;
justify-content: flex-end;
    span{
        font-size: 16px;
        color: #fff !important;
        margin-bottom: 0 !important; 
         @media(max-width: 992px){
             font-size: 10px;
         }
    }
    span:first-child{
        margin-right: 5px;
    }
`;

export default connect(null,{scrollTo})(Footer);
