export function lang(state = { lang: "en" }, action = {}) {
    switch (action.type) {
        case "SWITCH_LANG":
            return { lang: action.lang };
        default:
            return state;
    }
}

export function scrollTo(state = null, action = {}) {
    switch (action.type) {
        case "SCROLL_TO":
            return action.num;
        default:
            return state;
    }
}
