import React, {useEffect} from 'react';
import styled from 'styled-components';
import Slider from "react-slick";
//components
import ConcertCard from './ConcertCard';
import DetailsButton from '../../Common/DetailsButton'
import {connect} from "react-redux";
import {getConcertsHome} from "../../../Redux/actions/ConcertsHome";

function Concerts(props) {

    useEffect(() => {
        props.getConcertsHome(props.lang);
    }, []);

    return (
        <Container className="container-fluid">
            <h3>Concerts</h3>
            <div className="row">
                {
                    props.concertsHome.slice(0, 4).map((item, i) => {
                        return (
                            <ConcertCard {...item} key={i} concertHome={item}/>
                        )
                    })
                }
            </div>
            <DetailsButton text="See full schedule" route="concerts"/>
        </Container>
    );
}


const Container = styled.div`
  width: 100%; 
  //min-height: 100vh;
  min-height: calc(100vh - 183px);
  padding-top: 100px;
  padding-bottom: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h3 {
      font-size: 32px;
      text-align: center;
      color: #fff;
      margin-bottom: 50px;
      text-transform: capitalize;
      @media screen and (max-width: 1200px){ 
        margin-bottom: 32px; 
      }
      @media screen and (max-width: 576px){
        margin-bottom: 24px;
        font-size: 18px !important; 
      } 
      @media screen and (max-width: 992px){
      font-size: 24px
      } 
   }
`;

function mapStateToProps(state) {
    return {
        concertsHome: state.concertsHome,
        lang: state.lang.lang
    };
}

export default connect(mapStateToProps, {getConcertsHome})(Concerts);
