import React, {useState, useEffect, useRef} from 'react';
import {FormattedMessage} from 'react-intl';
import {connect} from 'react-redux';
import {withRouter} from 'react-router';
import {NavLink} from 'react-router-dom';
import {Link} from "react-scroll";
//actions
import {scrollTo} from "../../Redux/actions/lang";
import {indexPage} from "../../Redux/actions/setIndexPage";
import styled, {keyframes} from "styled-components";
import LangPack from "./LangPack";
import 'antd/dist/antd.css'
import $ from 'jquery'

function Nav(props) {
    const [menuState, setMenuState] = useState(null);
    const [active, setActive] = useState('');
    const [size, setSize] = useState(window.matchMedia("(max-width: 1200px)").matches);

    const navRef = useRef();
    useEffect(() => {
        if (Object.keys(props).length !== 0) {
            props.getRefElement(navRef, 'nav')
        }
    }, []);

    useEffect(() => {
        if (!size) {
            if (props.location.pathname !== "/") {
                navRef.current.style.transform = "translateY(0)";
            }
        }
    }, [props.location])

    function onMenu() {
        if(size){
            setMenuState(!menuState);
        }
    }

    function closeMenu() {
        if(size){
            setMenuState(false);
        }
    }

    function _onScroll(num) {
        props.scrollTo(num);
    }

    const addActiveClass = (e) => {
        const clicked = e.target.id;
        // $('#burger-menu').css('transform','translateX(0)');
        // $('#burger-menu').fadeOut();
        if(size){
            setMenuState(false)
        }
        if (props.index === clicked) {
            setActive('');
        } else {
            setActive(clicked);
        }
    };

    function renderMenu() {
        if (props.location.pathname === "/") {
            return (
                <ul>
                    <li>
                        <span className={`text-uppercase ${props.index == '1' ? 'active' : ''}`} id="1"
                              onClick={(e) => {
                                  _onScroll(2);
                                  addActiveClass(e);
                              }}
                              style={{textShadow: '0px 2px 4px rgba(0,0,0,.5)'}}>
                            <FormattedMessage id="biography"/>
                        </span>
                    </li>
                    <li>
                        <span onClick={(e) => {
                            _onScroll(3);
                            addActiveClass(e)
                        }} id="2"
                              className={`text-uppercase ${props.index == '2' ? 'active' : ''}`}>CONCERTS</span>
                    </li>
                    <li>
                        <span onClick={(e) => {
                            _onScroll(4);
                            addActiveClass(e)
                        }} id="3"
                              className={`text-uppercase ${props.index == '3' ? 'active' : ''}`}><FormattedMessage
                            id="press"/></span>
                    </li>
                    <li>
                        <span onClick={(e) => {
                            _onScroll(5);
                            addActiveClass(e)
                        }} id="5"
                              className={`text-uppercase ${props.index == '4' ? 'active' : ''}`}>TIMELINE</span>
                    </li>
                    <li>
                        <span onClick={(e) => {
                            _onScroll(6);
                            addActiveClass(e)
                        }} id="5" className={`text-uppercase ${props.index == '5' ? 'active' : ''}`}>MEDIA</span>
                    </li>
                    <li>
                        <span onClick={(e) => {
                            _onScroll(7);
                            addActiveClass(e)
                        }} id="6" className={`text-uppercase ${props.index == '6' ? 'active' : ''}`}>
                            <FormattedMessage id="contact"/>
                        </span>
                    </li>
                    <li>
                        <LangPack/>
                    </li>
                </ul>
            )
        } else {
            return (
                <ul>
                    <li>
                        <NavLink activeClassName="active" className="text-default text-uppercase" to="biography"
                                 onClick={closeMenu}>
                            <FormattedMessage id="biography"/>
                        </NavLink>
                    </li>
                    <li>
                        <NavLink activeClassName="active" className="text-default text-uppercase"
                                 to="/concerts" onClick={closeMenu}>CONCERTS</NavLink>
                    </li>
                    <li>
                        <NavLink activeClassName="active" className="text-default text-uppercase"
                                 to="/press" onClick={closeMenu}><FormattedMessage
                            id="press"/></NavLink>
                    </li>
                    <li>
                        <NavLink className="text-default text-uppercase"
                                 to={{pathname: "/", isScroll: true}} exact={true}
                                 onClick={closeMenu}>TIMELINE</NavLink>
                    </li>
                    <li>
                        <NavLink activeClassName="active" className="text-default text-uppercase"
                                 to="/media" onClick={closeMenu}>MEDIA</NavLink>
                    </li>
                    <li>
                        <NavLink className="text-default text-uppercase" to={{pathname: "/", isScrollContact: true}}
                                 exact={true} onClick={closeMenu}>
                            <FormattedMessage id="contact"></FormattedMessage>
                        </NavLink>
                    </li>
                    <li>
                        <LangPack/>
                    </li>
                </ul>
            )
        }
    }


    return (
        <Container className="container-fluid p-0"
                   style={{position: (props.location.pathname === "/") ? 'fixed' : 'static'}} ref={navRef}>

            <div className="row align-items-center menu-xl position-relative">
                <Logo className="col-12 col-md-4 col-xl-3">
                    <NavLink className="text-default" to={{pathname: "/", isScroll: false}}
                             onClick={() => _onScroll(1)}>
                        <span className="logo">Fuad Ibrahimov</span>
                    </NavLink>
                </Logo>
                <Menu className="col-md-9">
                    {
                        renderMenu()
                    }
                </Menu>

                <BurgerButton onClick={onMenu} id="burger-icons">
                    <Lines state={menuState}/>
                    <Circle state={menuState}/>
                </BurgerButton>

                <BurgerMenu state={menuState} id="burger-menu">
                    {
                        renderMenu()
                    }
                </BurgerMenu>

            </div>

        </Container>
    );
}

const Container = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.3s ease;
    left: 0;
    right: 0;
    z-index: 999;
    color: #fff;
    margin-top: 25px;
    .menu-xl{
        margin: 0 auto !important; 
        width: 100%;
     @media (max-width:1200px) {
        width: 100%;
      } 
    }

    .menu-responsive{ 
     @media (min-width:1200px) {
         display: none;
      }
    } 
`;

const BurgerButton = styled.div`
  width: 45px;
  height: 40px;
  position: absolute;
  top: 20px;
  right: 20px;
  display: none;
  cursor: pointer;
  z-index: 9999;
   @media screen and (max-width: 768px) {
      top: -2px;
      right: 33px !important;
  }
 
  @media screen and (max-width: 1200px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    top:1px;
    right: 23px !important;
  }
`;

const Lines = styled.div`
    width: 36px;
    height: 5px;
    background-color: white;
    position: relative;
    display: ${props => props.state ? 'none' : 'block'};
    
    &::before {
      content: '';
      width: 36px;
      height: 5px;
      background-color: white;
      position: absolute;
      top: 10px;
      right: 0;
    }
    
    &::after {
      content: '';
      width: 36px;
      height: 5px;
      background-color: white;
      position: absolute;
      top: -10px;
      right: 0;
    }
`;

const Circle = styled.div`
   width: 42px;
   height: 42px;
   border: 2px solid white;
   border-radius: 50%;
   display: ${props => props.state ? 'flex' : 'none'};
   position: relative;
   justify-content: center;
   align-items: center;
   
   &::before {
    content: '';
    width: 20px;
    height: 2px;
    position: absolute;
    background-color: white;
    transform: rotate(45deg);
   }
   
   &::after {
    content: '';
    width: 20px;
    height: 2px;
    position: absolute;
    background-color: white;
    transform: rotate(315deg);
   }
`;

const showMenu = keyframes`
  from {
    transform: translateX(270px);
  }
  to {
    transform: translateX(0);
  }
`;

const hideMenu = keyframes`
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(270px);
  }
`;

const BurgerMenu = styled.div`
box-sizing: border-box;
  width: 270px;  
  max-height: 100vh;
  height: 100vh;
  background-color: rgba(15,15,15,0.64);
  position: fixed; 
  top: -30px ;
  right: 0;
  z-index: 999;
  transform: translateX(276px);
  -webkit-backface-visibility: hidden;
  animation: ${props => props.state === null ? 'none' : (props.state ? showMenu : hideMenu)} 0.5s ease 0s forwards;
  
 ul{
        padding: 0; 
        list-style: none; 
        margin-bottom: 0;
        margin-top: 80px;
        li{
            font-size: 20px;
            padding: 8px 30px;  
            position: relative; 
             &:last-child {
              padding-top: 8px;
            }
        }
    }
`;

const Menu = styled.div`
font-size: 20px;
font-family: Montserrat-Regular;
     @media (max-width:1200px) {
      display: none; 
      }
    ul{
        padding: 0;
        //margin: 20px 50px 0 0;
        display: flex;
        list-style: none;
        justify-content: flex-end;
        margin-bottom: 0;
        li{
            font-size: 20px;
            padding: 10px 30px;
            cursor: pointer;
            //margin: 0 60px 0 0;
            position: relative;
            &:last-child {
              //margin-left: 41px;
              padding-right: 0;
            }
            span i {
              font-weight: 600;
            }
             @media (min-width: 1200px) and (max-width: 1300px){
                padding: 10px 8px
             }
              @media (min-width: 1300px) and (max-width: 1330px){
                padding: 10px 15px
             }
             @media (min-width: 1330px) and (max-width: 1494px){
                padding: 10px 20px
             }
        }
    }
`;

const Logo = styled.div` 
       @media screen and (min-width:769px) {
           text-align: center;
        } 
        word-spacing: 15px;
         @media screen and (min-width:769px) {
           word-spacing: 10px;
        } 
      a:hover{
      text-decoration: none;
      }
      span,a{
        font-family: "Stephanie-Marie-JF-Regular";
        font-size: 48px;
        color: #fff;
             @media(max-width: 1200px){
                font-size: 32px;
             }  
             @media(max-width: 768px){
                font-size: 28px;
             }
      }
      
       @media screen and (max-width: 1200px) {
          //padding-left: 70px !important;
       }
      
       @media screen and (max-width: 768px) {
          padding-left: 33px !important;
          text-align: left !important;
       }
       
      
`;

function mapStateToProps(state) {
    return {
        index: state.index
    }
}

export default connect(mapStateToProps, {scrollTo, indexPage})(withRouter(Nav));
