export const getConcertsHome = (lang) => async (dispatch) => {
    const concertsHome = await fetch(`http://fuadib.mircloud.host/api/concerts/main/${lang}`).then(res => res.json());
    dispatch(getConcertsHomeDispatch(concertsHome))
};

const getConcertsHomeDispatch = (concertsHome) => ({
    type: "GET_CONCERTS_HOME",
    concertsHome
});
