import React, {useEffect} from 'react';
import styled from 'styled-components';

//components
import PressCard from "./PressCard";
import DetailsButton from "../../Common/DetailsButton";
import {FormattedMessage} from "react-intl";
import {getPress} from "../../../Redux/actions/getPress";
import {connect} from "react-redux";

function Press(props) {


    useEffect(() => {
        props.getPress(props.lang);
    }, []);

    return (
        <Container className="container-fluid" id="press">
            <h3><FormattedMessage id="press" /></h3>
            <div className="row">
                {
                    props.press.slice(0,4).map((press, index) => {
                        return (
                            <PressCard {...press} key={index}/>
                        )
                    })
                }
            </div>
            <DetailsButton text="Read more" route="press"/>
        </Container>
    );
}


const Container = styled.div`
   min-height: calc(100vh - 183px);
   width: 100%;  
   display: flex;
   align-items: center;
   flex-direction: column;
   justify-content: center;
   color: #fff;  
   width: 100%;  
   padding-top: 100px;
   padding-bottom: 100px;
   h3 {
      font-size: 32px;
      text-align: center;
      color: #fff;
      margin-bottom: 50px;
      text-transform: capitalize;
      @media screen and (max-width: 1200px){ 
        margin-bottom: 32px; 
      }
      @media screen and (max-width: 576px){
        margin-bottom: 24px;
        font-size: 18px !important; 
      } 
      @media screen and (max-width: 992px){
      font-size: 24px
      } 
   }
    
   
`;


function mapStateToProps(state) {
    return {
        press: state.press,
        lang: state.lang.lang
    };
}


export default connect(mapStateToProps,{getPress})(Press);
